body {
    background-repeat: no-repeat;
    background-size: auto;
    height: 100vh;
    margin: 0;
    padding: 0;
    width: 100vw
}

div ul.navbar {
    align-content: stretch;
    display: flex;
    justify-content: space-evenly;
    list-style-type: none;
    padding-bottom: .5rem
}

div ul.navbar li {
    font-family: Ibarra Real Nova, serif;
    font-size: 1.5rem;
    font-weight: 600;
    height: 80px;
    padding-left: 30px;
    padding-right: 25px;
    padding-top: 10px;
    width: 140px
}

div ul.navbar li a {
    background-color: rgba(118, 60, 0, .35);
    -webkit-clip-path: polygon(16.27% 39.4%, 75% 25%, 75% 62.74%, 25% 75%);
    clip-path: polygon(16.27% 39.4%, 75% 25%, 75% 62.74%, 25% 75%);
    color: #7e3f27;
    padding: 20px 5rem;
    text-decoration: none
}

div ul.navbar li a:active,
div ul.navbar li a:hover {
    color: #ffbc59
}

#nav-deco {
    padding: 5px
}

h1,
p {
    font-family: Amatic SC, cursive;
    margin: 0
}

p {
    color: #000
}

a {
    color: #7e3f27;
    font-size: "Amatic SC", cursive;
    text-decoration: none
}

#about-deco,
#contact-deco,
#page-title,
#port-deco,
#resume-deco {
    align-items: center;
    display: flex;
    flex-direction: column;
    font-family: Amatic SC, cursive;
    font-size: 1.75em;
    justify-content: center;
    margin-left: 400px;
    margin-top: 75px;
    padding-top: 70px
}

#port-deco {
    font-size: 1.25em;
    margin-top: 50px
}

#contact-form {
    align-items: center;
    display: flex;
    flex-direction: column
}

label {
    clear: left;
    text-align: right;
    width: 250px
}

input,
label {
    float: left
}

#textarea #text-label {
    clear: left;
    float: left;
    text-align: right;
    width: 250px
}

#textarea #text-form {
    float: left
}

@media(max-width:900px) {
    #page-title {
        align-items: center;
        background-color: rgba(118, 60, 0, .55);
        -webkit-clip-path: polygon(16.27% 39.4%, 75% 25%, 75% 62.74%, 25% 75%);
        clip-path: polygon(16.27% 39.4%, 75% 25%, 75% 62.74%, 25% 75%);
        display: flex;
        font-family: Amatic SC, cursive;
        font-size: 1rem;
        justify-content: center;
        padding: 20px 7rem
    }
}

body {
    background-image: url(/public/Portfolio-React-6.png)
}